<template>
  <div id="app" @click="focusInput">
    <Bash :history="history" @new-line="newLine" />
  </div>
</template>

<script>
import Bash from './components/Bash'

export default {
  name: 'App',
  data() {
    return {
      command: '',
      history: [`gs-curriculum:~ guest-user$\nWelcome to my CV/Portfolio!\n\nUseful commands:\nbio\nskills\nportfolio\ncontact\ncommands\n\nIf you feel lost just type help and press enter :)\n\n`]
    }
  },
  watch: {
    history: function(val) {
      if(val) {
        setTimeout(function() {
          window.scrollTo(0,document.body.scrollHeight);
        }, 1);
      }
    }
  },
  components: {
    Bash
  },
  mounted() {
    (document).getElementById('input-data').focus()
  },
  methods: {
    newLine(command) {
      let display = ''
      switch(command.text) {
        case 'bio':
          display = "\n\nBorn in Padua on 26/01/1996\nFrom 2010 to 2015 I went to ITIS Francesco Severi (Computer Science Class) in Padua\nIn 2016/2017 I attended the Web Design Course @Mind Academy\nSince 2017 I am a Front End Developer @Webculture Creative Agency in Padua\n\n"
          break;
        case 'skills':
          display = '\n\nCoding:\n-HTML 5, CSS 3, Javascript (ES6), jQuery, GSAP\n-Javascript Framework: Vue.js\n-CSS Framework: Bootstrap, Tailwind CSS\n-Git, Github and NPM\n-Wordpress Theme Development and E-commerce solutions\n-PHP, MySQL, NoSQL dbs (Firebase RTDB, MongoDB)\n\nTools:\n-Adobe XD, Sketch, Illustrator, Photoshop\n-Microsoft Office\n\nLanguages\n-Italian (Mother Tongue)\n-English (Professional Working Proficiency)\n\nOthers:\n-Domain and Hosting Management, cPanel, SSL Certs\nWork in Progress: Node.js, Express\n\n\n'
          break;
        case 'contact':
          display = '\n\nYou can reach me at <a href="mailto:gianmaria.schinezos@gmail.com">gianmaria.schinezos@gmail.com</a> or you can call me at <a href="tel:3484848387">3484848387</a>\nFind me on <a href="https://www.linkedin.com/in/gianmaria-schinezos-18b091139/" target="_blank">linkedin</a>\nMy Telegram username is gnmrschnzs\n\nBy text or by voice, I will be very happy to hear from you!\n\n'
          break;
        case 'clear':
          this.history.length = 1
          break;
        case 'help':
          display = '\n\nThis is my bash styled CV/Portfolio\nYou can type a command and press enter to show the result\n\nFor a complete list of commands just type "commands"\n\n'
          break;
        case 'commands':
          display = '\n\n-bio\n-skills\n-portfolio\n-contact\n-credits\n-clear\n-commands\n-help\n\n'
          break;
        case 'portfolio':
          display = '\n\n<a href="https://skkskine.github.io/covid/" target="_blank">Covid-19 Data Representation (Vue.js w/ Axios)</a>\n<a target="_blank" href="https://skkskine.github.io/chat-realtime/">Realtime Chat (Vue.js + Firebase RTDB)</a>\nOther cool things are coming soon\n\n'
          break;
        case 'credits':
          display = '\n\nMade by Gianmaria Schinezos with Vue.js and some <3\n\n'
          break;
        default:
          display = 'command not found'
          break;
      }
      if(command.text != 'clear') {
        this.history.push(`gs-curriculum:~ guest-user$ ${command.text}`)
        this.history.push(`gs-curriculum:~ guest-user$ ${display}`)
      }
    },
    focusInput() {
      (document).getElementById('input-data').focus()
    }
  }
}
</script>

